<template>
  <div class="register_wrap">
    <!--头部-->
    <div class="header">
      <div class="icon"></div><div class="site_name">广告主</div>
      <div class="h_title">新用户注册</div>
      <router-link :to="'/login'">
        <el-button type="primary" class="loginBtn">登录/Login</el-button>
      </router-link>
    </div>
    <!-- 注册-->
    <div class="content">
      <div class="register_box">
        <el-form :model="registerForm" :rules="rules2" ref="registerForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="登录名" prop="userName">
            <el-input type="text" v-model="registerForm.userName" auto-complete="off"  placeholder="请输入6-12位字母或数字"></el-input>
          </el-form-item>
          <!--用户名字-->
          <el-form-item label="用户名" prop="userFullName">
            <el-input type="text" v-model="registerForm.userFullName" auto-complete="off"  placeholder="请输入您的姓名"></el-input>
          </el-form-item>

          <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="registerForm.pass" auto-complete="off" placeholder="请输入6-12位字母或数字"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="registerForm.checkPass" auto-complete="off" placeholder="再次输入密码"></el-input>
          </el-form-item>
          <el-form-item label="QQ" prop="qq" >
            <el-input type="text" v-model ="registerForm.qq" auto-complete="off" placeholder="请输入您的QQ号"></el-input>
          </el-form-item>

          <!-- new-->
          <el-form-item label="手机号" prop="phone" >
            <el-input type="text" v-model ="registerForm.phone" auto-complete="off" placeholder="请输入您的手机号"></el-input>
          </el-form-item>

          <el-form-item label="站长·商务·咨询" prop="consult" label-width="120px">
            <el-radio-group v-model="registerForm.consult">
              <span v-for="con_item in consultName" class="c_list">
                <el-radio :label="(con_item.id).toString()">
                  {{con_item.service_name}}<a :href="'tencent://message/?uin='+con_item.qq+'&Site=911广告联盟&Menu=yes'"><img class="relation" src="../image/qq.png" alt=""></a>
                </el-radio>
              </span>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="验证码" prop="code">
            <el-input type="text" v-model="registerForm.code" auto-complete="off" placeholder="请输入验证码"></el-input>
          </el-form-item>
          <div class="verification">
            <i class="ver_code" @click="changeCode" ref="ver_code">{{code}}</i>
          </div>
          <el-form-item>
            <el-button type="primary" @click="submitForm('registerForm')">立即注册</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue'
  import axios from 'axios'
  import qs from 'qs'
  import {Utils,ERR_OK} from 'common/js/Utils'

  export default {
    data() {
      var validateName = (rule, value, callback) => {
//          console.log(value.length)
        if (value === '') {
          callback(new Error('请输入登录名'));
        } else if ( value.length > 12 || value.length < 6) {
          callback(new Error('用户名的长度在6-12位'));
        } else if ( !(/^[A-Za-z0-9]*$/.test(value))) {
          callback(new Error('请输入数字或字母'))
        } else {
          callback();
        }
      };
      var validateFullName = (rule, value, callback) => {
        if(value===''){
          callback(new Error('请输入你的姓名'))
        }else if(!(/^[\u4e00-\u9fa5]{2,4}$/.test(value))){
          callback(new Error('请输入2-4位的汉字姓名'))
        }else{
          callback()
        }
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else if ( value.length > 12 && value.length < 6) {
          callback(new Error('请输入6-12位密码'));
        } else {
          if (this.registerForm.checkPass !== '') {
            this.$refs.registerForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.registerForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      var validateqq = (rule, value, callback) => {
        if (value === ''){
          callback(new Error('请输入qq号'));
        }else if ( !(/[1-9][0-9]{4,}/.test(value)) ){
          callback(new Error('qq号格式不正确！'))
        } else {
          callback();
        }
      };
      var validatephone = (rule, value, callback) => {
        if (value) {
          if (!(/^1[34578]\d{9}$/.test(value))) {
            callback(new Error('输入正确手机号!'))
          }else{
            callback()
          }
        }else{
          callback(new Error('请输入手机号！'))
        }
      };
      var verification = (rule, value, callback) => {
        if (value === ''){
          callback(new Error('请输入验证码'))
        }else if ( value.toUpperCase() !== this.code ) {
          callback(new Error('验证码不正确'))
        }else {
          callback();
        }
      }
      return {
        code:'',
        registerForm: {
          userName:'',
          userFullName:'',
          pass: '',
          checkPass: '',
          qq:'',
          phone:'',
          consult:0,
          code:''
        },
        consultName:'',
        rules2: {
          userName: [
            { validator: validateName, trigger: 'blur' ,required: true}
          ],
          userFullName:[
            {validator: validateFullName, trigger:'blur',required: true}
          ],
          pass: [
            { validator: validatePass, trigger: 'blur',required: true }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' ,required: true}
          ],
          qq: [
            { validator: validateqq, trigger: 'blur',required: true }
          ],
          phone: [
            {validator: validatephone, trigger: 'blur',required: true}
          ],
          consult: [
            { message: '请选择一位站长', trigger: 'change',required: true}
          ],
          code: [
            { validator: verification, trigger: 'blur',required: true}
          ]
        }
      };
    },
    created(){
      this.changeCode()
      this.getConsult()
    },
    methods: {
      submitForm(formName) {
        let self = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
//              console.log(typeof (this.registerForm.consult))
            let params = {
              'username':this.registerForm.userName,
              'password':this.registerForm.pass,
              'again_password':this.registerForm.checkPass,
              'qq':this.registerForm.qq,
              'service_id':this.registerForm.consult,
              'nickname':this.registerForm.userFullName,
              'phone': this.registerForm.phone
            }
//            console.log(params)
            this.axios.post(Utils.register,qs.stringify(params)).then((response) => {
//              console.log(response)
              let code = response.data.code
              if(code === ERR_OK){
                this.$message({
                  duration:1500,
                  message: '注册成功,即将跳转',
                  type:'success'
                });
                setTimeout(function () {
                  self.$router.push({path:'/login'})
                },400)
              }else if(code === 40010){
                this.$message({
                  duration:1500,
                  message: '登录名已存在',
                  type:'error'
                });
              }else{
                this.$message({
                  duration:1500,
                  message: '注册失败,信息有误',
                  type:'error'
                });
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },

      changeCode(){
        let selectChar = ['2','3','4','5','6','7','8','9','A','B','C','D','E','F','G','H','J','K','L','M','N','P','Q','R','S','T','U','V','W','X','Y','Z']
        this.code = ''
        for(let i=0;i<4;i++) {
          let charIndex = Math.floor(Math.random()*32);
          this.code += selectChar[charIndex];
        }

      },

      getConsult() {
        this.axios.post(Utils.consult,qs.stringify({
          'number':3,
        }))
          .then((response) => {
//            console.log(response)
            if (response.data.code === ERR_OK){
              this.consultName = response.data.data
            }
          })
          .catch(function (err) {
            console.log( err )
          })
      },

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .register_wrap
    .header
      width: 100%
      height: 80px
      box-sizing:border-box
      padding:0 50px
      overflow: hidden
      border-bottom: 1px solid #D6D7D9
      div
        float: left
        line-height: 80px
      .site_name
        padding:0 15px
      .h_title
        font-size: 16px
      .icon
        width: 60px
        height: 60px
        margin-top: 10px
        background: url(../image/Baikal_Logox.png) no-repeat
        background-size: 100%
      .loginBtn
        float: right
        margin-top:20px
    .content
      width: 100%
      padding: 30px
      box-sizing: border-box
      background: #f5f5f5
      .register_box
        border: 1px solid #ddd
        width: 900px
        margin: 0 auto
        padding: 60px 30px
        background: #fff
        border-radius: 7px
        .el-form
          width:500px
          .c_list
            margin:0 8px
            .relation
              width: 16px
              padding:0 5px
              vertical-align: text-top
        .verification
          width: 100px
          margin-left: 100px
          margin-bottom: 20px
          i
            display: block
            height: 40px
            line-height: 40px
            width:100px
            text-align:center
            background:#f3f3f3
            cursor: pointer
</style>



